import * as React from "react";
import { Link } from "gatsby";
import { Card } from "react-bootstrap";
import kebabCase from "lodash/kebabCase";

const BlogArticle = ({ id, date, title, excerpt, slug, tags }) => {
  return (
    <div>
      <Card className="blog-article" key={id}>
        <Card.Body>
          <Card.Title>
            <Link to={`/blog/${slug}`} className="blog-title-link">
              {title}
            </Link>
          </Card.Title>
          <Card.Text>
            <div>
              <div className="blog-article-date">{date}</div>
              <div className="blog-article-content">
                {excerpt}
                &nbsp; <Link to={`/blog/${slug}`}>read more ➝</Link>
              </div>
            </div>
            {tags.map((tag, i) => (
              <Link
                to={`/tags/${kebabCase(tag)}/`}
                className="btn btn-secondary btn-sm mr-5"
                style={{ marginRight: "5px", marginBottom: "2px" }}
                key={i}
              >
                {tag}
              </Link>
            ))}
          </Card.Text>
        </Card.Body>
      </Card>
    </div>
  );
};

export default BlogArticle;
